.footer {
   background-color: $white;

   .footer-wrapper {
      padding-block: 15rem;
      position: relative;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      @media (max-width: $tablet) {
         padding-block: 15rem 5.2rem;
         flex-direction: column;
         gap: 5.7rem;
      }

      .pseudo-cross {
         position: absolute;
         width: 100%;
         left: 30%;
         top: 0;

         @media (max-width: $mobile) {
            left: 50%;
            transform: translateX(-50%);
         }
      }
   }

   &__title {
      color: $green;
      margin-bottom: 10rem;

      @media (max-width: $tablet) {
         margin-bottom: 3.5rem;
      }
   }

   &__emails-wrapper {
      display: flex;
      flex-direction: row;
      gap: 7.2rem;
      margin-bottom: 6.3rem;

      @media (max-width: $tablet) {
         gap: 6.3rem;
         margin-bottom: 4.8rem;
      }
   }

   &__emails-title {
      color: $black;
      font-family: $mossport;
      font-size: 3.5rem;
      font-weight: 400;
      line-height: 140%; /* 49px */
      text-transform: uppercase;
      margin-bottom: 2.5rem;

      @media (max-width: $mobile) {
         font-size: 2.4rem;
         margin-bottom: 0.6rem;
      }
   }

   &__emails-items a {
      color: $black;
   }

   &__info {
      color: $black;
      font-family: $myriad;
      font-size: 2rem;
      font-weight: 400;
      line-height: 110%; /* 22px */
   }

   &__info-text {
      margin-bottom: 1.5rem;
   }

   &__info li {
      margin-left: 0.5rem;
   }


   &__right-side {
      align-self: end;
      display: flex;
      flex-direction: column;
      justify-content: end;
      gap: 15.3rem;

      @media (max-width: $tablet) {
         flex-direction: column-reverse;
         align-self: normal;
         justify-content: start;
         gap: 4.7rem;
      }

      .dots {

         @media (max-width: $tablet) {
            align-self: flex-end;
         }


         @media (max-width: $mobile) {
            width: 16.4rem;
         }
      }
   }

   .social-media-links {
      display: flex;
      flex-direction: row;
      justify-content: end;
      gap: 1rem;
      margin-bottom: 2.13rem;

      @media (max-width: $tablet) {
         justify-content: start;
      }
   }

   .social-media-docs {
      display: flex;
      flex-direction: column;
      justify-content: end;
      gap: 0.4rem;

      @media (max-width: $tablet) {
         justify-content: start;
      }
   }

   .social-media-docs a {
      color: $green;
      text-align: right;
      font-family: $myriad;
      font-size: 1.4rem;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      text-transform: uppercase;
      text-decoration: none;
      opacity: 0.4;
      transition: 0.3 ease;

      &:hover {
         opacity: 1;
      }

      @media (max-width: $tablet) {
         text-align: left;
      }
   }
}




















